.admin-panel {
  display: flex;
  flex: 1;
  background: #f2f2f2;
  min-height: 100vh; /* Utilisez "min-height" au lieu de "height" pour s'adapter au contenu */
}
  
  .sidebar {
    width: 15%;
    background: rgb(7, 2, 58);
    color: #fff;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .sidebar h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .sidebar ul {
    list-style: none;
    padding: 0;
   
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .sidebar li {
    cursor: pointer;
    padding: 10px; 
    /* text-align: center; */
    height: 20%;
   
    /* font-size: 16px; */
    transition: 0.8s;
  }
  
  .sidebar li.active {
    background: #0074d9;
    border-radius: 10px;
  }
  
  .admin-content {
    flex: 1;
    display: flex; /* Utilisez une disposition en colonnes */
    flex-direction: column;
    margin: 6% 0 0 0;
    min-height: 85vh;
  }
  .table-head{
    height: 10%;
    background-color: rgb(7, 2, 58);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center ;
  }
  .table-head h2{
    margin: 0;
    padding: 0;
    width: 60%;
    text-align: center;
  }
  .table-head .recherche{
    width: 20%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center ;
  
    
  }
  .table-head .recherche form{
    width: 100%;
  }
  .table-head .recherche input{
    height: 30px;
    background-color: #fff;
    border: 0;
    border-radius: 0 25px 25px 0;
    width: 100%;
   outline: none;
  }
  
  .recherche .icon{
    background-color: aqua;
    height: 32px;
    padding: 0 4%;
    display: flex;
    align-items: center ;
    border-radius: 25px 0 0 25px;
  }
/* Style du conteneur de la table */
.table {
  background: #fff;
 
 
  flex: 1;
  margin-top: -100px; /* Ajoute de l'espace en haut du tableau */
  overflow: auto; /* Ajoute une barre de défilement en cas de contenu débordant */
}

/* Style de la table */
table {
  width: 100%; /* Le tableau occupe toute la largeur disponible */
  border-collapse: collapse;
  border-radius: 50px;
}

/* Style des cellules de la table */
th, td {
  border: 1px solid #ccc; /* Ajoute des bordures aux cellules avec une couleur plus claire */
  padding: 12px; /* Augmente la taille de la police pour un meilleur espacement */
  text-align: center;
  color: #333; /* Couleur du texte légèrement plus foncée */
}

/* Style de l'en-tête de la table */
th {
  background: #0074d9; /* Couleur de fond de l'en-tête */
  color: #fff; /* Couleur du texte de l'en-tête en blanc */
  font-weight: bold; /* Texte en gras pour l'en-tête */
}
/* CSS pour réduire la taille des boutons de pagination */
.pagination-button {
  font-size: 14px; /* Taille de la police */
  padding: 5px 10px; /* Rembourrage (ajustez selon vos besoins) */
  margin: 5px; /* Marge entre les boutons (ajustez selon vos besoins) */
}

.pagination-current-page {
  font-size: 16px;
  color: #333; /* Taille de la police pour le numéro de la page active */
}
/* Conteneur de la pagination */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  
}
/* Style pour aligner les boutons de gauche à droite sous chaque entrée du tableau */
.actions-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Style pour les boutons */
.actions-container button {
  margin: 5px; 
  border: 0;
  background: 0;
  cursor: pointer;
  
}
.actions-container button:hover{
transition:0.2s;
  transform: scale(1.1);
}
.action-icon{
  font-size: 22px;
}
.add{
  position: absolute;
  width: auto;
  font-size: 30px;
  padding: 10px 15px;
  border-radius: 50px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  bottom: 5%;
  right: 2%;
  background-color: rgb(6, 86, 161);
  cursor: pointer;
}
.deconnexion-button{
  border: 0;
  background-color: rgb(219, 60, 60);
  color: #fff;
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 4%;
  border-radius: 5px;
  margin-top: 80%;
  cursor: pointer;
}
.confirm-button{
  border: 0;
  background-color: #c7c4c4af;
  padding: 3% 10%;
  margin-top: 20px;
  border-radius: 5px;
  /* box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; */
 cursor: pointer;
}
.confirm-button:hover{
  
  background-color: rgb(255, 0, 0);

}
