.masterclassreservation {
    width: 100%;
    height: calc(100vh - 80px);
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.masterclassreservation .masterclasscontent{
    width: 100%;
    height: 100%;
   
   
    display: flex;
    justify-content: space-between;
  
}

.masterclassreservation .masterclasscontent .leftview span{
    font-size: 35px;
    font-weight: 600;
    position: absolute;
    top: 20px;
    left: 40px;
    height: 100px;
    position: relative;
}
.masterclassreservation .masterclasscontent .leftview{
    position: relative;
    background: transparent;
    width: 25%;
    margin-left: 2%;
    
}

.masterclassreservation .masterclasscontent .rightview{
    background: transparent;
    width: 75%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.rightview .rightview-header {
    width: 90%;
    height: 30%;
    margin-bottom: 3%;
    background: transparent;
    position: relative;
  
    border-bottom: 3px solid rgb(207, 85, 85);
}
.petit-titre{
  margin-top: 5%;
}
.rightview .rightview-footer {
    width: 90%;
    height: 10%;
    background: transparent;
    position: relative;
    display: flex;
    gap: 10px;
    align-items: center;
}

.rightview .viplaces {
    width: 90%;
    height: 50%;
    /* background: #000000a2; */
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
}

.viplace,
.selected-viplace,
.personne-viplace,
.reserved-viplace{
    width: 25px;
    height: 25px;
    margin: 3px;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
}
.viplace {
    background: white;
}

.personne-viplace {
    background: #00aaff !important;
}

.lignebas
{
    position: absolute;
    bottom: 22%;
    width: 90%;
    height: 2px;
    left: 5%;
    background: black;
}

.selected-viplace
{
    background: #0cbd0cd4;
}
.reserved-viplace
{
    background: #ff0000e2;
}

.rightview-footer .button {
    display: flex;
    align-items: center;
    justify-content: center;
    /* height: 38px;
    width: 100px; */
    border-radius: 8px;
    font-size: 16px;
    font-weight: 600;
    background: #1e93d2;
    cursor: pointer;
    position: absolute;
    right: 25px;
    bottom: 15px ;
}
.rightview-footer .button:hover{
  transform: scale(1.1);
  transition: 0.5s;
}
.rightview-header .persreserver {
    width: 40%;
    height: 35px;
    border-radius: 10px;
    background: #ffffff24;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    position: absolute;
    right: 5px;
    bottom: 10px;
}
.nomdescmc {
    font-size: 20px;
}

.datanomdescmc {
    font-size: 16px;
    font-weight: 400;
}
.rightview-header .masterclassedate {
  width: 50%;
  height: 70%;
  /* background: red; */
  margin-top: 3%;
}
.titlemasterclass{
  font-size: 40px;
  padding-bottom: 5%;
}
.hoursmasterclass {
  display: flex;
  gap: 5px;
  margin-left: 5%;
}
.daymasterclass{
  display: flex;
  flex-direction:row ;
  gap: 50px;
  margin: 0% 0 2% 0;
  padding-left: 3%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.327);
  width:50%;
 
}
.daymasterclass div{
  cursor: pointer;
  border: none;
  margin: 0;
}
.daymasterclass div:hover{
  color: #00aaff;
  border-bottom: 2px solid #00aaff4b;
}
.daymasterclass .active{
  color: #00aaff;
  
  border-bottom: 2px solid #00aaff;
}
.hoursmasterclass .active {
  background: #0cbd0cd4 !important;
}
.hoursmasterclass div:hover {
  background: #0cbd0c72 !important;
}
.hoursmasterclass .hourmasterclass{
  padding-block: 8px;
  padding-inline: 12px;
  font-weight: 400;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
}

/* Media Query pour les écrans de petite taille */
@media (max-width: 768px) {
  .masterclassreservation .masterclasscontent {
    flex-direction: column;
    position: relative;
    align-items: center;
    gap: 15px;
  }
  .leftview, .rightview {
    min-height: 100% !important;
    /* background: blue !important; */
  }
  .leftview {
    width: 115% !important;
    margin-left: 0 !important;
  }
.rightview-header, .rightview-footer {
    width: 98% !important;
  }
  .rightview {
    width: 100% !important;
    padding-block: 10px;
   
    margin-bottom: 10px !important;
  }
  .rightview-header .masterclassedate{
    margin: 0 0 5% 5% ;
    width: 100%;
  }
  .daymasterclass{
    width: 80%;
  }
  .viplace, 
  .personne-viplace, 
  .selected-viplace, 
  .reserved-viplace {
    width: 20px !important;
    height: 20px !important;
  }
  .rightview-footer {
    padding-block: 10px !important;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: flex-start !important;
    align-items: flex-start !important;
    height: calc(10% + 40px) !important;
  }
  .rightview-header {
    height: calc(30% + 100px) !important;
  }
  .viplaces {
    width: 100% !important;
    background: transparent !important;
    height: calc(60% + 100px) !important;
  }
  .nomdescmc {
    font-size: 14px !important;
  }
  .datanomdescmc{
    font-size: 16px !important;
    font-weight: 400 !important;
  }
  .btnplaypausemc {
    top: 62% !important;
  }
  .rightview-header .persreserver{
    width: 95%;
  }
}