.formulaire{
  overflow: hidden;
  width: 65%;
  height: 85vh;
  /* box-shadow: 0 20px 25px rgba(0, 0, 0, 0.4); */
  background-color: rgba(252, 252, 252, 0.842);
  margin: 4% 0 0 20%;
  backdrop-filter: blur(4px);
  border-radius: 25px;
  display: flex;
  flex-direction: row;
  /* border: 1px solid #ffffffc4; */
}
.formulaire.jeux{
  width: 60%;
  height: 70vh;
  margin-top: 7%;
}
.formulaire.jeux form{
  padding-top: 5% ;
}
.formulaire form{
  width: 60%;
  position: absolute;
 
  margin-left:38% ;
  padding: 5% 0 1% 0;
}
.form-group, .form-group-file{
  position: relative;
  margin-bottom: 6%;
}
.form-group-file{
  width: 100%;
}
.form-group input{
  width: 98.5% ;
  display: block;
  padding: 5px;
  border: none;
  width: 88%;
  background: 0 ;
  font-size: 16px;
 border-width: 0;
 border-style: none;
  
  outline: none;
  height: 2%;
  margin-top: 2%;
}
.form-group input:focus ~ label, 
.form-group input:valid ~ label{
  transform: translateY(-20px);
  font-size: 14px;
  color: #000000;
}
.form-group label{
  position: absolute;
  pointer-events: none;
  bottom: 10px;
  font-size: 16px;
  transition: all 0.3s ease;
  color: rgb(0, 0, 0);
  font-weight: 400;
}
.form-group .underline{
  position: absolute;
  bottom: 0;
  height: 1.5px;
  width: 100%;
  background: rgb(0, 0, 0);
}
.form-group .underline:before{
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  background: #000000;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.3s ease;
}
.form-group  input:focus ~ .underline:before,
.form-group  input:valid ~ .underline:before{
  transform: scale(1);
}
.form-group select,.form-group-file label{
 width: 85%;
 padding: 10px;
 background: 0;
 background-color: rgba(232, 232, 232, 0.573); 
 font-weight:400 ;
  border-bottom :1px solid rgb(0, 0, 0);

}
