} */

.form-group label, .label{
  font-size: 12px;
  
  color: rgba(3, 3, 3, 0.856); 
  font-weight: 400;
  position: absolute;
			top: -7px;
			font-size: 14px;
			white-space: nowrap;
			
			text-align: left;
			left: 15px;
			padding: 0 5px;
			color: #999;
