.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  height: 100vh;
  width: 100%; /* Ajout de cette ligne pour occuper toute la largeur de l'écran */
  /* background:  url('fte.png'); */
  background-size: contain;
  background-repeat: no-repeat;
  backdrop-filter: blur(20px);
}
  
.login-boite{
  width: 25%;
  height: 45vh;
  background-color: rgba(247, 247, 247, 0.177);
  /* padding: 0 2%; */
  border-radius: 25px;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(20px);
  border: 1px solid #ffffff6b;

}
.login-boite h1{
  background-color: rgba(255, 255, 255, 0.424);
  margin: 0;
  padding: 5%;
  text-align: center;
  font-weight: 400;
  text-transform: uppercase;
}
  .login-form {
   
    padding: 10px 20px 0 20px;
    border-radius: 10px;
  
    
  }
  
  .login-form input {
   margin-top: 10%;
   height: 15%;
   color: #fff;
   width: 20vw;
  }
  .login-form label , .login-form input:focus ~ label ,.login-form input:valid ~ label{
    color: #fff;
  }
.login-form .underline, .login-form .underline::before{
    background-color: #fff;
    color: #fff;
    width: 20vw;
  }
  .login-form button {
    width:60%;
    padding: 10px;
    background: rgba(152, 153, 153, 0.8);
    margin: 10% 0 0 20%;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;
    transition: background 0.3s;
    letter-spacing: 2px;
  }
  
  .login-form button:hover {
    background: rgba(0, 116, 217, 1);
  }
 .login-container .alert-message{
  width: 40%;
 }
  
  