.fte-pro{
    background-color:#0a011bc8 ;
    /* background: url('../../img/linear-bg2.jfif')  ; */
    padding:2% 0;
    /* height: 70vh; */
  }

  .pro-about-titre h2{
    font-size: 32px;
 }
 .pro-about-titre h2::after {
   content: "";
   position: absolute;
   width: 0%;
   height: 3px;
   display: block;
   transition: all 0.3s ease;
   /* bottom: 25%; */
   width: 5%;
   height: 3px;
 }

 .GrandSlide {
    display: flex;
    gap: 20px;
    margin: 1% 0 10% 10%;
    width: 80%;
    height: 50vh;
    overflow: hidden;
    
  }
  
  .GrandSlide-pro {
     width: 70%;
    border-radius: 50px;
  }
  
  .GrandSlide-img {
    max-width: 100%;
    height: 100%; 
    border-radius: 25px;
    overflow: hidden;
  
  }
  .GrandSlide-img .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
  
  .PetitSlide-pro {
    align-items: center;
    width: 45%;
    height: 100%;
    border-radius: 9px;
    position: relative;
  }
  


  .PetitSlide-pro .haut{
    font-size: 70px ;
   
  }
  .PetitSlide-pro .bas{
    font-size: 70px ;
 
  }
  .PetitSlide-pro .bas span{
    font-size: 20px ;
    
  }
  .PetitSlide-pro .volet {
     font-size: 18px ;
     text-align: center;
      width: 40%;
      padding: 1% 3%;
      margin: 5% 0 0 55%;
     background-color: #f8931b;
  }

  .bordure-droite{
    border-top: 5px solid #fefefe;
    border-right: 5px solid #fefefe;
    width: 10%;
    height: 12%;
    margin-left: 65%;
  }
  .bordure-gauche{
    border-bottom: 5px solid #ffffff;
    border-left: 5px solid #fefefe; 
    width: 10%;
    height: 12%;
    position:absolute;
    bottom: 0;
    /* margin-top: 55%; */
  }
.text-container{
  opacity: 0;
  transform: translateY(-20%); 
  transition: opacity 0.5s ease-in-out, transform 1s ease-in-out;
  position: absolute;
  margin-left: 6%;
}
.text-container.slideshow {
  opacity: 1;
  transform: translateY(0); 
}



