/* Jeu.css */

.jeu .jeuImage{
  width: 100%;
  
}
.jeu-content {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  /* background-color: #f5f5f5; Ajout d'une couleur de fond */
}

/* Styles pour le menu de jeux */
.menu-jeux {
  display: flex;
  gap: 30px;
  margin-top: 20px;
  background-color: #fff; /* Couleur de fond */
  padding: 0 3%; /* Espace intérieur */
  border-radius: 50px; /* Coins arrondis */
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.jeu-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 16px;
  padding: 10px;
  color: #333; /* Couleur du texte */
  text-transform: uppercase;

  font-weight: 500;
  
}

.jeu-button:hover {
  

  color: #007bff; /* Couleur au survol */
}

/* Style du jeu actif */
.jeu-button.active {
  
  
  color: #007bff;
}

/* Styles pour le slider vertical */
.jeu-slider-container {
  position: relative;

  width: 55%;
  margin: 3% 0;
  padding: 5% 0;
 
  overflow: hidden;
  
}

/* Styles pour les flèches de navigation "Up Arrow" et "Down Arrow" */
.up-arrow,
.down-arrow {
  font-size: 24px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #007bff; Couleur de fond */
  color: #fff; /* Couleur du texte */
  border-radius: 50%; /* Coins arrondis */
  transition: background-color 0.3s, transform 0.3s, bottom 0.3s; /* Ajout de la transition */
}

.up-arrow {
 top: 0;
  transform-origin: center top;
}

.down-arrow {
  bottom: 0;
  transform-origin: center top;
  
}


.up-arrow:hover {
  background-color: #0057b398; /* Couleur au survol */
  color: #fff;
}

.down-arrow:hover {
  background-color: #0057b398; /* Couleur au survol */
  color: #fff;
}

 .slick-list.slick-track{
  height: auto;
 }
.game-name {
 height: 40vh;
  /* padding: 4% 0; */
   /* opacity: 0; */
  display: flex !important;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  transition: transform 0.3s, filter 0.3s;
  
}
 
.game-name img {
 width: 30%;
 height: auto;
 object-fit: cover;
}
.game-descri{
  width: 50%;
  margin-left: 7%;
}

.game-name h3 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.game-name p {
  font-size: 16px;
  
  margin-bottom: 10px;
}

/* Style du nom du jeu actif dans le slider */
.game-name.active {
  /* opacity: 1; */
  background-color: transparent;/* Couleur de fond */
  /* transform: scale(1.1); */
}


/* Centrer l'élément actif du slider vertical */
.slick-slide.slick-center .game-name.active {
  transform: translateY(0);
  max-height: 100% !important;
}

/* Styles pour le bouton "Participer" */
.participer-jeu {
  background-color: #007bff;
  color: #fff;
  border: none;
  text-decoration: none;
  padding: 1% 3%;
  margin-left: 43%;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
  transition: background-color 0.3s;
  animation: coolAnimation 1s ease-in-out infinite alternate;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 10%;
  text-align: center;
}

.participer-jeu:hover {
  transform: scale(1.2);
}

/* 
.bouton-participer {
  animation: coolAnimationParticiper 1s ease-in-out infinite alternate;
  border-radius: 50px;
  width: 100%;
  padding: 1.4%;
  margin-top: 5%;
  background-color: #ff6600; 
  border-color: transparent;
} */

@keyframes coolAnimation {
  0% {
    transform: scale(1);
    box-shadow: 0 0 5px rgba(70, 167, 232, 0.7);
  }
  100% {
    transform: scale(1.01); /* Légère agrandissement au cours de l'animation */
    box-shadow: 0 0 15px rgba(70, 167, 232, 0.7);
  }
}

