.centered-form {
    display: flex;
    flex-direction: column;
  
    height: 6 0vh;
    width: 40%;
    
    background: rgba(255, 255, 255, 0.795) ;
    backdrop-filter: blur(10px);
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }
  
  .form-group {
    margin-bottom: 20px;
    width: 100%;
  }
  
  /* label {
    display: block;
    
  } */
  .inputform{
    width: 80%;
    
    margin: 4% 20px;
    position: relative;
  }
  .inputform input{
    width: 98.5% ;
    display: block;
    padding: 5px;
    border: none;
    width: 98.5%;
    background: 0 ;
    font-size: 16px;
   border-width: 0;
   border-style: none;
    
    outline: none;
    height: 2%;
    margin-top: 2%;
  }
  .inputform input:focus ~ label, 
  .inputform input:valid ~ label{
    transform: translateY(-20px);
    font-size: 14px;
    color: #000000;
  }
  .inputform label{
    position: absolute;
    pointer-events: none;
    bottom: 10px;
    font-size: 16px;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, 0.508);
    font-weight: 400;
  }
.underline {
    position: absolute;
    bottom: 0;
    height: 1.5px;
    width: 100%;
    background: rgb(0, 0, 0);
  }

  .underline.file{
   bottom: 45%;
   width: 71%;
   height: 1.5px;
  }
 
 .underline:before {
    position: absolute;
    content: "";
    height: 2px;
    width: 100%;
    background: #000000;
    transform: scaleX(0);
    transform-origin: center;
    transition: transform 0.3s ease;
  }
  .inputform input:focus ~ .underline:before,
  .inputform input:valid ~ .underline:before{
    transform: scale(1);
  }
 .inputgroup  input[type='date'], .inputgroup select {
    width: 98.5%;
    padding: 5px;
    font-size: 16px;
   border-width: 0;
   border-style: none;
 
    outline: none;
    transition: border-color 0.2s ease;
    margin-top: 5px;
  }
  
  input:focus {
    border-color: #007BFF;
  }
  
  .button {
    background-color: #007BFF;
    color: #fff;
    padding: 12px 24px;
    border-radius: 4px;
    font-size: 18px;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin-top: 10px;
  }
  
  
  .success-message,
  .error-message {
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
  }
  
  .success-message {
    color: #28a745;
  }
  
  .error-message {
    color: #dc3545;
  }
  @media screen  and (max-width: 768px) and (min-width: 550px ) {
    .inputform input{
      height: 5%;
      margin-top: 7%;
    }
   
  }
  @media screen  and (max-width: 549px) {
    .centered-form.stand{
      margin: 20% 3%;
      width: 80vw;
      
    }
  }