.centre-form {
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  height: 80vh;
  width: 50vw;
  background: rgba(255, 255, 255, 0.795) ;
  backdrop-filter: blur(10px);
  border-radius: 10px;
  padding: 20px 20px 20px 10%;
  margin-top: 10%;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.error-message,.success-message{
  /* background-color: #0066ff; */
  position: absolute;
  width: 40% ;

  text-align: center;
  padding: 5% 2%;
  top: 30%;
  left: 25%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.error-message button,.success-message button{
  padding: 3% 4%;
  border: 0;
  margin: 5% 0 0 70%;
  border-radius: 5px;
  cursor: pointer;
}
.error-message button:hover,.success-message button:hover{
  background-color: rgb(246, 33, 33);
  color: #fff;
}
.success-message {
  background-color:rgb(174, 234, 174) ;
  color: green;
  margin-top: 10px;
  height: 7vh;
}

.error-message {
  background-color: rgb(229, 177, 177);
  color: red;
  margin-top: 10px;
  height: 10vh;
}
.alert-message{
  position: absolute;
  width: 20% ;
 z-index: 33;
  text-align: center;
  padding: 3% 2%;
  top: 35%;
  left: 45%;
  color: #000;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.alert-message .error-button{
  background: 0;
  border: 0;
  background-color: rgba(255, 0, 0, 0.59);
  color: #fff;
  font-size: 20px;
  position: absolute;
 top: 0;
 right: 0;
 cursor: pointer;
}
.alert-message .error-button:hover{
  background-color: rgb(255, 0, 0);
}
.inputgroup{
  width: 80%;
  margin: 2% 3%;
}
.inputgroup label{
  font-weight: 400;
  font-size: 14px;
  color: #000;

}
.inputgroup input{
  background:0;
  border-bottom: 2px solid #000 !important;
  border-radius: 0;
  
 
}



.inputgroup input[type='date'],.inputgroup input[type='number']{
  color: #000;
  font-size: 14px;
  font-weight: 400;
}
.inputgroup input[type='number']{
  width: 10%;
  border-bottom: 2px solid #000;
}
.inputgroup select,.inputgroup input[type = 'file']{
  background: 0;
  border-bottom: 2px solid #000 !important;
  width: 100%;
    padding: 10px;
    font-size: 14px;
   border-width: 0;
   border-style: none;
   color: #000000ba;
    outline: none;
    transition: border-color 0.2s ease;
    margin-top: 0;
}
.input-submit {
  background: #0066ff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
 
}
.input-bouton{
  display: flex;
  flex-direction: row;
  width: 60%;
  gap: 30px;
  
  margin-left: 4%;
}
.input-bouton input{
  width: 60%;
  border: 0;
  color: #fff;
  border-radius: 8px;
}
.annuler{
  background-color: rgba(255, 0, 0, 0.552);
  padding: 10px;
  cursor: pointer;
}
.reserver{
  background-color: #0066ff82;
  padding: 10px;
  cursor: pointer;
}
.annuler:hover{
  background-color: rgb(255, 0, 0);
}
.reserver:hover{
  background-color: #0066ff;
}

@media screen  and (max-width: 768px) and (min-width: 550px ) {
  .centre-form{
    height: 55vh !important;
  
    padding: 20px 5%;
    
  }

  .underline.file{
    width: 75%;
    bottom: 50%;
  }
  .popup{
z-index: 10 !important;
  }
  .input-bouton{
  flex-direction:  column-reverse;
  width: 100%;
  gap: 5px;
  margin-left: 0;
 
}
.input-bouton input{
  width: 90%;
  
}
  
}
@media screen  and (max-width: 549px)  {
 
  .centre-form{
    
    height: 75vh !important;
   
    width: 80vw;
    padding: 20px 5%;
    
  }

  .underline.file{
    width: 75%;
    bottom: 50%;
  }
  .popup{
      z-index: 10 !important;
      display: block !important;
      padding-left: 5%;
  }
  .input-bouton{
  flex-direction:  column-reverse;
  width: 100%;
  gap: 5px;
  margin-left: 0;
 
}
.input-bouton input{
  width: 90%;
  
}
  
}