.popup-content{
  background-color: rgb(255, 255, 255) !important;
  width: 25% !important;
  height: 20vh !important;
  border: 0 !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px !important;
}
.bouton-close{
  position: absolute;
  top: -12%;
  right: -4%;
 border-radius: 50px ;
 padding-top: 1%;
  font-size: 30px;
  border: 0;
  background-color: #f22222;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  cursor: pointer;
}

.centre-form.partenaire{
  height: 65vh;
  width: 40vw;
}

.centre-form.partenaire .formgroup{
 margin-bottom: 7%;
}
@media screen  and (max-width: 768px){
  .centre-form.partenaire{
    height: 40vh !important;
   
  }
}