.hackathon{
    position: relative;
    z-index: 1;
}
.background {

    height: 80vh;
    background-color: rgba(3, 1, 23, 0.8);
 
}
.background img{
    object-fit: cover;
    width: 100%;
    height: 80vh;
    z-index: -1;
  
    opacity: 0.2;
   
}


.contenu{
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 35%;
    
}
.ordinateur{
    width: 50%;
}
.ordinateur img{
    width: 100%;
    
}

.content{
    width: 40%;
    margin-top: 8%;
    margin-left: 5%;
}
.content h2{
    font-weight: 300;
    font-size: 26px;
    margin: 0;
    letter-spacing: 5px;
}
.content h1{
    background: linear-gradient(to right, #f8931b, #ff3700,#ff0000);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 42px;
    margin: 0;
}
.content p{
    margin-bottom: 8% !important;
}
.participer-button {
    padding: 2% 6%;
    margin-top: 5%;
    width: 15%;
    text-align: center;
    text-decoration: none;
    background-color: rgba(104, 76, 228, 0.817);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
  
.participer-button:hover {
   scale: 1.1;
   transition: 0.5s;
  
  }
  @media screen and (max-width : 850px) and (min-width : 551px) {
    .background {

        height: 60vh;
      
     
    }
    .background img{
        
        height: 60vh;
    }
  }
  @media screen and (max-width : 550px)and (min-width : 451px){
    .background {

        height: 60vh;
      
     
    }
    .background img{
        
        height: 60vh;
    }
    .contenu{
        
        bottom: 55%;
        
    }
    .content{
        margin-top: 0;
    }
    .content h2{
        
        font-size: 20px;
      
    }
    .content h1{
       
        font-size: 32px;
        
    }
    .content p{
        font-size: 12px;
    }
    .participer-button{
        width: 25%;
    }
  }
  @media screen and (max-width : 450px)  {
    .background {

        height: 80vh;
      
     
    }
    .background img{
        
        height: 80vh;
    }
    .contenu{
        flex-direction: column;
        bottom: 50%;
        
    }
    .ordinateur{
        width: 100%;
    }
    .ordinateur img{
        width: 100%;
        
    }
    .content{
        margin-left: 15%;
        width: 70%;
    }
    .content h2{
        
        font-size: 30px;
      
    }
    .content h1{
       
        font-size: 38px;
        
    }
    .content p{
        font-size: 14px;
    }
    .participer-button{
        width: 25%;
    }
  }